import React from "react";
import { View, Text, ScrollView, StyleSheet, Dimensions } from "react-native";
import { LineChart } from "react-native-chart-kit";

const attendanceData = [
  { name: "Timothy", Aug: "游릭", Sep: "游댮", Oct: "游릭", Nov: "游릭", Dec: "游릭", Jan: "游리", Feb: "游릭", notes: "Missed Sep due to illness, Jan reason not given" },
  { name: "Aisha", Aug: "游릭", Sep: "游릭", Oct: "游댮", Nov: "游릭", Dec: "游릭", Jan: "游릭", Feb: "游댮", notes: "Oct family emergency, Feb was traveling" },
  { name: "Daniel", Aug: "游댮", Sep: "游릭", Oct: "游리", Nov: "游릭", Dec: "游댮", Jan: "游릭", Feb: "游릭", notes: "Aug had flu, Oct reason not given, Dec personal issue" },
  { name: "Mercy", Aug: "游릭", Sep: "游릭", Oct: "游릭", Nov: "游댮", Dec: "游댮", Jan: "游릭", Feb: "游릭", notes: "Nov and Dec sick leave" },
  { name: "Rahul", Aug: "游댮", Sep: "游댮", Oct: "游릭", Nov: "游릭", Dec: "游릭", Jan: "游리", Feb: "游댮", notes: "Aug & Sep out of town, Jan reason not given, Feb flu" },
];

const months = ["Aug", "Sep", "Oct", "Nov", "Dec", "Jan", "Feb"];
const attendanceTotals = months.map((month) => {
  let present = 0, absent = 0;
  attendanceData.forEach((student) => {
    if (student[month] === "游릭") present++;
    else absent++;
  });
  return { month, present, absent };
});

const AttendanceTable = () => {
  return (
    <ScrollView style={styles.container}>
      <Text style={styles.title}>Attendance Record (Aug 2024 - Feb 2025)</Text>
      <ScrollView horizontal>
        <View style={styles.tableContainer}>
          <View style={[styles.row, styles.headerRow]}>
            <Text style={styles.headerCell}>Student</Text>
            {months.map((month) => <Text key={month} style={styles.headerCell}>{month}</Text>)}
            <Text style={styles.headerCell}>Notes</Text>
          </View>
          {attendanceData.map((student, index) => (
            <View key={index} style={[styles.row, index % 2 ? styles.rowOdd : styles.rowEven]}>
              <Text style={styles.cell}>{student.name}</Text>
              {months.map((month) => <Text key={month} style={styles.cell}>{student[month]}</Text>)}
              <Text style={styles.notes}>{student.notes}</Text>
            </View>
          ))}
          <View style={[styles.row, styles.totalRow]}>
            <Text style={styles.totalCell}>Total</Text>
            {attendanceTotals.map(({ month, present, absent }) => (
              <Text key={month} style={styles.totalCell}>{`游릭${present} | 游댮${absent}`}</Text>
            ))}
            <Text style={styles.totalCell}>-</Text>
          </View>
        </View>
      </ScrollView>
      <Text style={styles.chartTitle}>Attendance Trends</Text>
      <LineChart
        data={{
          labels: months,
          datasets: [
            { data: attendanceTotals.map(item => item.present), color: () => "#2ECC71" },
            { data: attendanceTotals.map(item => item.absent), color: () => "#E74C3C" },
          ],
        }}
        width={Dimensions.get("window").width - 30}
        height={250}
        yAxisLabel=""
        chartConfig={{
          backgroundGradientFrom: "#fff",
          backgroundGradientTo: "#fff",
          decimalPlaces: 0,
          color: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`,
        }}
        bezier
        style={styles.chart}
      />
      <View style={styles.keyContainer}>
        <Text style={styles.keyTitle}>Key:</Text>
        <Text style={styles.keyItem}>游릭 Present</Text>
        <Text style={styles.keyItem}>游댮 Absent (With Reason)</Text>
        <Text style={styles.keyItem}>游리 Absent (No Reason Given)</Text>
      </View>
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  container: { padding: 10, backgroundColor: "#f8f9fa" },
  title: { fontSize: 20, fontWeight: "bold", textAlign: "center", marginBottom: 10 },
  tableContainer: { borderWidth: 1, borderColor: "#ddd", borderRadius: 8, overflow: "hidden" },
  row: { flexDirection: "row", borderBottomWidth: 1, borderColor: "#ddd" },
  headerRow: { backgroundColor: "#007bff" },
  rowEven: { backgroundColor: "#f0f0f0" },
  rowOdd: { backgroundColor: "#ffffff" },
  headerCell: { flex: 1, fontSize: 14, fontWeight: "bold", color: "#fff", padding: 10, textAlign: "center" },
  cell: { flex: 1, fontSize: 14, padding: 10, textAlign: "center" },
  notes: { flex: 2, fontSize: 12, padding: 10, textAlign: "left", color: "#555" },
  totalRow: { backgroundColor: "#dfe6e9", borderTopWidth: 1, borderColor: "#aaa" },
  totalCell: { flex: 1, fontSize: 14, fontWeight: "bold", padding: 10, textAlign: "center" },
  keyContainer: { marginTop: 15, padding: 10, backgroundColor: "#fff", borderRadius: 5, elevation: 2 },
  keyTitle: { fontSize: 16, fontWeight: "bold", marginBottom: 5 },
  keyItem: { fontSize: 14, marginBottom: 2 },
  chartTitle: { fontSize: 16, fontWeight: "bold", textAlign: "center", marginVertical: 15 },
  chart: { marginVertical: 10, borderRadius: 10 },
});

export default AttendanceTable;